/* -------------------------------------------------------------------------
  Color variables for both light and dark mode.
---------------------------------------------------------------------------- */
/* Light mode base colors */
:root {
  --very-dark-blue: #111930;
  --dark-blue: #232339;
  --mid-blue: #356fce;
  --light-blue: #bedeff;
  --very-light-blue: #e6f2ff;
  --dark-green: #027353;
  --mid-green: #038c33;
  --light-green: #68bb13;
  --yellow: #f2b94f;
  --orange: #f58e36;
  --mid-red: #cc4444;
  --light-red: #ffeeee;
  --black: #1d1d1d;
  --very-very-dark-grey: #232323;
  --very-dark-grey: #2f2f2f;
  --dark-grey: #3e3e3e;
  --grey: #9a9a9a;
  --light-grey: #dadada;
  --very-light-grey: #f1f1f1;
  --very-very-light-grey: #f8f8f8;
  --white: #ffffff;
}

/* Dark mode base color overrides */
@media (prefers-color-scheme: dark) {
  :root {
    --black: #070710;
    --very-very-dark-grey: #0e0e19;
    --very-dark-grey: #191923;
    --mid-blue: #55b7ff;
    --orange: #ec8329;
    --mid-red: #ff4343;
    --dark-red: #250201;
    --yellow: #ffb834;
    --mid-green: #0ebf4c;
    --light-green: #96ff4c;
    --dark-green: #0c946e;
  }
}
/* Light mode semantic colors */
:root {
  --page-background: var(--white);
  --page-text: var(--black);
  --link-text: var(--mid-blue);
  --link-highlight: var(--mid-red);
  --header-background: var(--very-dark-blue);
  --header-text: var(--white);
  --header-link-text: var(--white);
  --header-link-highlight: var(--yellow);
  --post-rule: var(--light-grey);
  --what-is-background: var(--very-light-blue);
  --what-is-border: var(--mid-blue);
}

/* Dark mode semantic color overrides */
@media (prefers-color-scheme: dark) {
  :root {
    --page-background: var(--black);
    --page-text: var(--white);
    --header-background: var(--very-dark-blue);
    --post-rule: var(--dark-grey);
    --what-is-background: var(--dark-blue);
  }
}
/* -------------------------------------------------------------------------
  Basic element styles, generic across the whole page.
---------------------------------------------------------------------------- */
* {
  box-sizing: border-box;
}

body {
  color: var(--page-text);
  background-color: var(--page-background);
  font-family: -apple-system, "BlinkMacSystemFont", "SF Hello", "Segoe UI", "Roboto", "Helvetica Neue", "Arial", sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  font-size: 16px;
  line-height: 1.4;
}

h1 {
  margin: 0;
  padding: 0;
  font-size: 23px;
  line-height: 1.2;
}

h2 {
  font-size: 25px;
  line-height: 1;
  letter-spacing: -0.5px;
}

h3 {
  font-size: 19px;
  line-height: 1.2;
}

h4 {
  font-size: 14px;
  line-height: 1.2;
}

a {
  color: var(--link-text);
  text-decoration: none;
}

a:hover {
  color: var(--link-highlight);
}

strong {
  font-weight: 600;
}

small {
  font-size: 11px;
}

pre {
  border: 1px var(--code-snippet-border) solid;
  background-color: var(--code-snippet-background);
  color: var(--code-snippet-text);
  font-size: 14px;
  border-radius: 4px;
  padding: 15px;
  overflow: scroll;
}

blockquote {
  padding: 10px;
  border-left: 2px var(--grey) solid;
}
blockquote p {
  margin: 0;
}

hr {
  margin: 40px 0;
  border: 0;
  height: 2px;
  background-color: var(--rule-background);
}

section {
  margin: 35px 0;
}

main picture.shadow img {
  box-shadow: 0px 3px 8px 2px rgba(100, 100, 100, 0.2);
}
main img {
  margin: 40px auto;
  display: block;
  max-width: 80%;
}

/* -------------------------------------------------------------------------
  Site layout and containers.
---------------------------------------------------------------------------- */
.inner {
  margin: 0 auto;
  padding: 20px 10px;
  max-width: 800px;
}

.development {
  padding: 10px;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  background-color: var(--mid-red);
  color: var(--white);
}

/* -------------------------------------------------------------------------
  Site header and footer.
---------------------------------------------------------------------------- */
/* Common */
header,
footer {
  color: var(--header-text);
  background-color: var(--header-background);
}
header a,
footer a {
  padding: 3px;
  font-size: 15px;
  font-weight: 600;
  color: var(--header-link-text);
}
header a:hover,
footer a:hover {
  color: var(--header-link-highlight);
}
header nav ul,
footer nav ul {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
}
header nav ul li,
footer nav ul li {
  margin: 0 5px;
  list-style: none;
}

/* Header specific */
header .inner {
  display: flex;
  flex-direction: column;
  align-items: center;
}
header h1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-bottom: 20px;
}
header h1 img {
  margin-bottom: 15px;
  max-width: 96px;
}
header nav a {
  font-size: 14px;
  border-bottom: 1px var(--header-link-text) solid;
}
header nav a:hover {
  border-color: var(--header-link-highlight);
}

/* Footer specific */
footer nav {
  display: flex;
  flex-direction: column;
  align-items: center;
}
footer nav p {
  margin: 3px 0;
}
footer nav p a {
  font-size: 13px;
}
footer small a {
  font-size: 12px;
}
footer ul {
  margin: 10px;
}

/* -------------------------------------------------------------------------
  Styles specific for blog posts or the blog layout in general.
---------------------------------------------------------------------------- */
h2.post {
  margin-bottom: 0;
}
h2.post a {
  color: var(--page-text);
}

hr.post {
  margin: 5px 0 20px 0;
  height: 1px;
  background-color: var(--post-rule);
}

.what {
  margin-bottom: 40px;
  padding: 15px;
  border: 1px var(--what-is-border) solid;
  border-radius: 5px;
  background-color: var(--what-is-background);
}

.dim {
  opacity: 40%;
}